import React, { useMemo } from 'react'
import { AiOutlineMenu, AiOutlineClose } from "react-icons/ai";
import {
  Drawer,
  DrawerBody,
  DrawerFooter,
  DrawerContent,
  useDisclosure,
  Icon,
  Flex,
} from "@chakra-ui/react"
import { Link } from 'react-scroll'

import Box from 'components/Box';
import Button from 'components/Button';
import theme, { Media, responsive } from '../components/ThemeProvider/theme';
import useCsvButton from './useCsvButton'

const links = [
  {
    name: '兩大焦點',
    to: 'section-focus'
  },
  {
    name: '永續三贏',
    to: 'section-subject'
  },
  {
    name: '基金檔案',
    to: 'section-fund'
  },
]

const offset = 48

const Menu = () => {
  const buttons = useCsvButton()
  const button = useMemo(() => buttons.data?.filter(d => d.id === 'header-vid') || [], [buttons])
  const { isOpen, onOpen, onClose } = useDisclosure()
  return (
    <>
    <Media greaterThanOrEqual="tablet">
        <Flex alignItems="center">
          {button.lenngth > 0 && (
            <Box mx="1.875em">
              <Button.Transparent
                color="black"
                fontSize="1em"
                fontWeight="normal"
                py="0"
                px="0"
              >
                {button[0]?.name}
              </Button.Transparent>
            </Box>
          )}
          {links.map(({ name, to }) => (
            <Box cursor="pointer" fontSize="1.125em" fontWeight="bold" mx="1.875em">
              <Link smooth to={to} offset={-offset}>{name}</Link>
            </Box>
          ))}
          <Box mx="1.875em">
            <Button
              as={Link}
              cursor="pointer"
              smooth
              bg="white"
              to="section-buy"
              border="1px solid"
              color="custom.orange"
              fontSize="1.125em"
              borderRadius="1.25em"
              _hover={{ bg: 'white' }}
              offset={-offset}
            >申購請洽</Button>
          </Box>
        </Flex>
      </Media>
      <Media lessThan="tablet">
      <Box.Relative
        as={Button}
        borderRadius="0"
        bg={isOpen ? 'custom.blue' : 'white'}
        color={isOpen ? 'white' : 'custom.blue'}
        _hover={{
          bg: 'custom.blue',
          color: 'white',
        }}
        _focus={{
          bg: 'white',
          color: 'custom.blue',
        }}
        onClick={onOpen}
        height={theme.headerHeight}
        width={theme.headerHeight}
        px="0"
        aria-label="選單"
      >
        <Icon as={isOpen ? AiOutlineClose : AiOutlineMenu} w={responsive('1.25rem', '2rem')} h={responsive('1.25rem', '2rem')} />
      </Box.Relative>
      <Drawer
        isOpen={isOpen}
        placement="right"
        onClose={onClose}
        size="md"
        preserveScrollBarGap
      >
        <DrawerContent bg="custom.blue" style={{ top:theme.headerHeight }} height="auto">
          <DrawerBody height="100%">
            {button.map(({ name }, i) => (
              <Box key={i}>
                <Button.Transparent
                  width="100%"
                  py="1.5rem"
                  borderBottom="1px solid white"
                  borderRadius="0"
                >{name}</Button.Transparent>
              </Box>
            ))}
            {links.map(({ name, to }, i) => (
              <Box key={i}>
                <Button.Transparent
                  width="100%"
                  offset={-offset}
                  py="1.5rem"
                  as={Link}
                  smooth
                  to={to}
                  borderBottom="1px solid white"
                  borderRadius="0"
                  onClick={onClose}
                >{name}</Button.Transparent>
              </Box>
            ))}
          </DrawerBody>
          <DrawerFooter>
            <Button
              as={Link}
              smooth
              to="section-buy"
              width="100%"
              bg="custom.orange"
              fontSize={responsive('1em', '1.25em')}
              py="1rem"
              _hover={{ bg: 'custom.orange' }}
              offset={-offset}
              onClick={onClose}
            >申購請洽</Button>
          </DrawerFooter>
        </DrawerContent>
      </Drawer>
      </Media>

    </>
  )
}

export default Menu
